.App {
    text-align: center;
    background: white;
    width: 98%;
    margin: 1%;
    border-radius: 4px;
    min-height: 98%;
}

#root {
    width: 100%;
    height: 100%;
}

body {
    background: #003366;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

.extraPadding {
    padding: 4px;
}

p {
    padding-left: 3px;
}

tr:hover {
    border-top: #aaaaaa 1px dashed;
    border-bottom: #aaaaaa 1px dashed;
    background-color: #f9f9f9;
}

body {
    margin: 0;
    padding: 0;
}

* {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}

.page {
    width: 21cm;
    min-height: 29.7cm;
    padding: 0.4cm;
    margin: 0.1cm auto;
    border: 1px #d3d3d3 solid;
    border-radius: 5px;
    background: white;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    margin-top: 1px;
    background-image: url('./images/cog1.png'), url('./images/cog1.png'), url('./images/cog1.png'), url('./images/cog1.png');
    background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
    background-position: left top, left bottom, left top, right top;
}

.subpage {
    padding: 1cm;
    /*border: 5px red solid;*/
    height: 256mm;
    outline: 2cm/*#FFEAEA solid*/
    ;
}

@page {
    size: A4;
    margin: 0;
}

@media print {
    html,
    body {
        width: 210mm;
        height: 297mm;
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
        background: white;
    }
    .no-print,
    .no-print * {
        display: none !important;
    }
    .page {
        margin: 0;
        border: initial;
        border-radius: initial;
        width: initial;
        min-height: initial;
        box-shadow: initial;
        page-break-after: always;
        background-image: url('./images/cog1.png'), url('./images/cog1.png'), url('./images/cog1.png'), url('./images/cog1.png');
        background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
        background-position: left top, left bottom, left top, right top;
    }
}

.characterSheet {
    text-align: left;
}

.characterSheet button {
    height: 18px;
    font-size: 11px;
}

.characterSheet h5 {
    height: 20px;
}

.pageTop {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.pageTopInner {
    margin: 0;
    margin-bottom: -3px;
    width: 21cm;
    left: auto;
    right: auto;
}

.tab {
    background: none;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    background-color: white;
}

.tabActive {
    font-weight: 900;
    border-bottom: none;
}

table.th20 th {
    width: 2%;
    border: none;
    text-align: right;
    border-bottom: 1px dotted lightgrey;
}

th {
    text-align: left;
}

table.th20 td {
    width: 2%;
    border: none;
    text-align: right;
    border-bottom: 1px dotted lightgrey;
}

table.th20 {
    background-color: aliceblue;
}

div.divcol2 {
    width: 100%;
    margin: 0 auto;
    display: -webkit-flex;
    /* Safari */
    display: flex;
    /* Standard syntax */
}

div.divcol2 div {
    padding: 10px;
    background: #dbdfe5;
    -webkit-flex: 1;
    /* Safari */
    -ms-flex: 1;
    /* IE 10 */
    flex: 1;
    /* Standard syntax */
    width: 300px;
}

div.divcol2 div:first-child {
    background: white;
}

.flexbox {
    display: flex;
    flex-direction: row;
}

.flexContainer {
    width: 25%;
}

div.bordersection {
    border: 1px black dotted;
}

a.notalink {
    text-decoration: none;
    color: black;
    cursor: auto;
}